import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MaterialModule } from '@angular/material';
import { FileUploadModule } from 'ng2-file-upload';


import { AwesomePipe } from '../pipes/awesome.pipe';
import { CloudinaryImageComponent } from '../thirdparty/cloudinary-image.component';
import { CloudinaryImageService } from '../thirdparty/cloudinary-image.service';
import { HighlightDirective } from '../directives/highlight.directive';
import { Broadcaster } from '../services/broadcaster.service';
import { O365SecurityService } from '../services/o365Security.service';
@NgModule({
  imports: [CommonModule, MaterialModule.forRoot()],
  declarations: [AwesomePipe, HighlightDirective,CloudinaryImageComponent],
  exports: [ AwesomePipe, HighlightDirective,CloudinaryImageComponent,
             CommonModule, FormsModule, ReactiveFormsModule,
             HttpModule, MaterialModule, 
             FileUploadModule
  ],
  providers: [Broadcaster, O365SecurityService,CloudinaryImageService]
})
export class SharedModule { }




