import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from "@angular/router"
import { DomSanitizer } from '@angular/platform-browser';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph"
import { Subscription } from 'rxjs/Subscription';
import { Filter_Data, Filter_Params } from '../../../Models/student.model'
@Component({
  templateUrl: './students.cmp.html',
  styleUrls: ['./students.cmp.scss']
})
export class StudentsCmp implements OnInit, OnDestroy {

  activeTabIndex = 0;
  dynamicTabs = [
    {
      label: 'All'
    },
    {
      label: 'Ayush Jaiswal'
    },
    {
      label: 'Ben Halverson'
    },
    {
      label: 'Mayank Arya'
    }
  ];
  tempUrl: string = 'assets/libs/download.jpeg';
  students: any[] = [];
  subscription_get_students: Subscription;
  constructor(private _router: Router,
    private _dataRouting: DataRouting,
    private _broadcaster: Broadcaster,
    private _DomSanitizationService: DomSanitizer) {
  }
  ngOnInit() {
    this.registerEvents();
  }
  ngOnDestroy() {
    this.unRegisterBroadcast();
  }
  registerEvents() {
    this.subscription_get_students = this._broadcaster.on<string>('get_students')
      .subscribe((result: any) => {
        this.students = result;
      })
  }
  unRegisterBroadcast() {
    this.subscription_get_students.unsubscribe();
  }
  tabChanged() {
    let event_data: any = {};
    if (this.activeTabIndex === 0) {
      event_data.action = 'All';
      this._dataRouting.uiEvents2(event_data);
      return;
    }
    let filter_params_instructor: Filter_Params = { worksheet_index: "4", 
                                                    table_index: "3",
                                                    column_index: "3",
                                                    criteria:this.dynamicTabs[this.activeTabIndex].label }
    let filter_data_instructor = this._dataRouting.getFilterData(filter_params_instructor);
    event_data.action = 'filter';
    event_data.url = filter_data_instructor.filter_url;
    event_data.http_data = filter_data_instructor.filter_criteria;
    event_data.filtered_url = filter_data_instructor.visiable_url;
    this._dataRouting.uiEvents2(event_data);
  }

  gotoUser(id) {
    this._router.navigate(['/', id]);
  }
}


