import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, Validators, FormGroup, FormControl, FormArray } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { FileUploader } from 'ng2-file-upload';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph";
import { CloudinaryUploader } from '../../../../shared/thirdparty/cloudinary-uploader.service';
import { CloudinaryOptions } from '../../../../shared/thirdparty/cloudinary-options.class';
import { Student, Analysis } from '../../../Models/student.model'
@Component({
    templateUrl: './student.detail.cmp.html',
    styleUrls: ['./student.detail.cmp.scss']
})
export class StudentDetailCmp implements OnInit, OnDestroy {
    selectedUser: any = {};
    isPhotoUploading: boolean;
    public_id: string = "";
    version: string = "";
    image_id: string = "";
    uploader: CloudinaryUploader;
    options: CloudinaryOptions;
    my_form: FormGroup;
    instructors = [];
    goals = [];
    currentStudent: any = {};
    isNewAnalysis: boolean;



    subscription_register_instructors: Subscription;
    subscription_get_student_data: Subscription;
    subscription_get_analysis_data: Subscription;
    constructor(private _fb: FormBuilder, private _route: ActivatedRoute, private _broadcaster: Broadcaster, private _dataRouting: DataRouting) {
        this.options = _dataRouting.options;
        this.uploader = new CloudinaryUploader(this.options);
    }

    ngOnInit() {

        this.uploader.onAfterAddingFile = this.onAfterAddingFile.bind(this);
        this.uploader.onSuccessItem = this.onSuccessItem.bind(this);
        let id = (this._route.snapshot.params['id']);
        if (id) {
            // this.selectedUser = this._dataRouting.getUser(id);
        }
        else {
            // this.studentData.url = 'assets/libs/download.jpeg';
        }
        this.registerEvents();

        this.my_form = this._fb.group({
            instructor: ['', Validators.required],
            age: ['', Validators.required],
            goal: ['', Validators.required],
            height: ['', Validators.required],
            analysiss: this._fb.array([
                // this.initAnalysis()
            ])
        })
        this.instructors = this._dataRouting.instructorsTest;
        this.goals = this._dataRouting.goals;
    }

    initAnalysis(data: any) {
        if (data) {
            return this._fb.group({
                month: [data.month, Validators.required],
                weight: [data.weight, Validators.required],
                fat: [data.fat, Validators.required],
            });
        }
        else {
            return this._fb.group({
                month: ['', Validators.required],
                weight: ['', Validators.required],
                fat: ['', Validators.required],
            });
        }

    }

    addAnalysis(data: any): FormGroup {
        const control = <FormArray>this.my_form.controls['analysiss'];
        this.isNewAnalysis = true;
        let newAnaylysis: FormGroup = this.initAnalysis(data);
        control.push(this.initAnalysis(data));
        return newAnaylysis;
    }

    removeAnalysis(i: number) {
        const control = <FormArray>this.my_form.controls['analysiss'];
        control.removeAt(i);
    }

    ngOnDestroy() {
        this.unRegisterEvents();
    }

    registerEvents() {

        this.subscription_get_student_data = this._broadcaster.on<string>('get_student_data')
            .subscribe((result: any) => {
                this.currentStudent = this._dataRouting.currentStudent;
                if (this.currentStudent && this.currentStudent[0] != "") {
                    //todo:remove hard coding for indexs
                    this.public_id = 'v' + this.currentStudent[6] + '/' + this._dataRouting.currentUser.id;
                    (<FormControl>this.my_form.controls['instructor']).setValue(this.currentStudent[2], { onlySelf: true });
                    (<FormControl>this.my_form.controls['age']).setValue(this.currentStudent[3], { onlySelf: true });
                    (<FormControl>this.my_form.controls['goal']).setValue(this.currentStudent[4], { onlySelf: true });
                    (<FormControl>this.my_form.controls['height']).setValue(this.currentStudent[5], { onlySelf: true });
                }
                else {
                    this.selectedUser.url = 'assets/libs/download.jpeg';
                }
            })
        this.subscription_get_analysis_data = this._broadcaster.on<string>('get_analysis_data')
            .subscribe((result: any) => {
                if (this._dataRouting.currentAnalysis) {
                    this._dataRouting.currentAnalysis.map((item: any, index: number) => {
                        if (index !== 0) {
                            this.addAnalysis({ "month": item[1], "weight": item[2], "fat": item[3] });
                        }
                    })
                    // let analysiss: any = (<FormArray>this.my_form.controls['analysiss']).controls[0];
                    // (<FormControl>analysiss.controls['month']).setValue(this._dataRouting.currentAnalysis[1], { onlySelf: true });
                    // (<FormControl>analysiss.controls['weight']).setValue(this._dataRouting.currentAnalysis[2], { onlySelf: true });
                    // (<FormControl>analysiss.controls['fat']).setValue(this._dataRouting.currentAnalysis[3], { onlySelf: true });
                }
            })
    }

    unRegisterEvents() {
       // this.subscription_register_instructors.unsubscribe();
        this.subscription_get_student_data.unsubscribe();
        this.subscription_get_analysis_data.unsubscribe();
    }

    onAfterAddingFile(fileItem: any) {

        var reader = new FileReader();
        reader.addEventListener("load", (e: any) => {
            this.selectedUser.url = e.target.result
        }, false);

        if (fileItem && fileItem.some.type.match('image.*')) {
            reader.readAsDataURL(fileItem.some);
        }
    }

    onSuccessItem(item: any, response: string, status: number, headers: any) {
        this.isPhotoUploading = false;
        let test: any = JSON.parse(response);
        this.submit(test.version)
    }

    createChart() {
        let event_data: any = {};
        event_data.action = 'chart';
        event_data.url = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/charts/add';
        event_data.http_data = {
            "type": "ColumnStacked",
            "sourceData": "A1:E5",
            "seriesBy": "Auto"
        }
        this._dataRouting.uiEvents2(event_data);
    }

    submit(imageVersionNumber: string) {
        let event_data: any = {};
        if (this.currentStudent && this.currentStudent[0] != "") {
            let row_index = +this.currentStudent[1] - 2;
            event_data.action = 'student_update';
            event_data.url = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet4/tables/3/rows/itemAt(index=' + row_index + ')';
            event_data.http_data = {
                "values": [[
                    this._dataRouting.currentUser.userPrincipalName,
                    '=Row()',
                    this.my_form.value.instructor,
                    this.my_form.value.age,
                    this.my_form.value.goal,
                    this.my_form.value.height,
                    imageVersionNumber,
                    this._dataRouting.currentUser.id]]
            };
            // event_data.temp_data = { "values": [[this.studentData[0],this._dataRouting.currentUser.userPrincipalName,this.my_form.value.instructor,this.my_form.value.age,this.my_form.value.goal,this.my_form.value.height,imageVersionNumber ]] }
            this._dataRouting.uiEvents2(event_data);
        }
        else {
            event_data.action = 'student_add';
            event_data.url = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet4/tables/3/rows/add";
            event_data.http_data = {
                "values": [[
                    this._dataRouting.currentUser.userPrincipalName,
                    '=Row()',
                    this.my_form.value.instructor,
                    this.my_form.value.age,
                    this.my_form.value.goal,
                    this.my_form.value.height,
                    imageVersionNumber,
                    this._dataRouting.currentUser.id
                ]]
            }
            this._dataRouting.uiEvents2(event_data);
        }
        // if (this.isNewAnalysis) {
        //     event_data.action = 'student_add';
        //     event_data.url = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/tables/5/rows/add";
        //     event_data.http_data = {
        //         "values": [
        //             [
        //                 '=Row()',
        //                 this._dataRouting.currentUser.userPrincipalName,
        //                 this.my_form.value.analysiss[this.my_form.value.analysiss.length - 1].month,
        //                 this.my_form.value.analysiss[this.my_form.value.analysiss.length - 1].weight,
        //                 this.my_form.value.analysiss[this.my_form.value.analysiss.length - 1].fat,
        //             ]
        //         ]
        //     }
        //     this._dataRouting.uiEvents2(event_data);
        // }
        // this.Imagesubmit();
    }
    //
    Imagesubmit() {
        if (this.uploader.queue.length > 0) {
            this._dataRouting.signCloudinary(this._dataRouting.currentUser.id);
            this.isPhotoUploading = true;
            let file: any = this.uploader.queue[this.uploader.queue.length - 1];
            this.uploader.uploadItem(file);
        }
        else {
            this.submit(this.currentStudent[6]);
        }
    }
}




